.app{
  margin-top: 30px;
}


.navbar-nav > li > a,.navbar-inverse .navbar-form, .navbar-inverse .navbar-brand  {
    padding-top: 0px;
    padding-bottom: 0px;
    margin: 0;
    line-height: 80px;
}

@media (max-width: 767px) {
    .navbar-nav > li > a {
    line-height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;}
}
